import React, { Component } from 'react'

export default class Map extends Component {
    state={
        clictHeight:null   
    }
    componentDidMount(){
        let clictHeight=document.documentElement.clientHeight || document.body.clientHeight;
        this.setState({clictHeight})
    }
    render() {
        const { clictHeight } = this.state
        const { address } = this.props.location.state
        return (
            <iframe
                title="resg"
                src={address}
                style={{ width: '100%', border: '0px', height:clictHeight+'px'}}
                sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
                scrolling="auto"
            />
        )
    }
}
